export const mainResponse = (rawData, request) => {
    if (rawData.error) {
        return {
            data: {},
            services: {
                ipfs: {},
                geth: {}
            },
            error: { message: rawData.error.message }, request
        };
    }
    return {
        data: rawData,
        services: {
            ipfs: {},
            geth: {}
        }, request
    };
};
